<template>
  <div class="top">
    <img src="@/assets/images/my/reg.png" alt="">
    <div class="phone">
      <van-field
      v-model="name"
      center
      clearable
      placeholder="请输入真实姓名">
        <!-- <van-button slot="button" size="small" type="primary">发送验证码</van-button> -->
      </van-field>
    </div>
    <div class="phone mt0">
      <van-field
      v-model="phone"
      center
      clearable
      placeholder="请输入手机号码"/>
    </div>

    <div class="reg" @click="submit()">立即注册</div>

    <!-- 协议 -->
    <div class="xieyi">
      <van-radio checked-color="#15BDDB" name="1" :value="value" @click="ready()">注册即代表阅读并同意</van-radio>
      <span @click="isshow=true">《机大呷充电使用协议》</span>
    </div>

    <!-- 协议弹出层 -->
    <van-popup v-model="isshow" id="pop">
      <div class="xy">
        html片段
      </div>
    </van-popup>
  </div>
</template>

<script>
import { bang_phone } from '@/axios/getData'
import { Icon, Popup, Dialog, Field, Button, Radio, Toast } from 'vant';
// import url from "@/axios/api.js";
// import axios from "axios";   
export default {
  data(){
    return{
      data:[],
      value:'1',          //  默认已阅读
      isshow:false,       // 
      name:'',            //  用户名
      phone:'',           //  手机电话号码 
    }
  },
  components: {
    'van-icon': Icon,
    "van-popup": Popup,
    vanField: Field,
    vanButton: Button,
    vanRadio: Radio
  },
  created() {

  },
  methods:{
    //  阅读协议
    ready(){
      if(this.value){
        this.value = '1';
        return;
      }  
      this.value='1';
    },
    //  立即注册
    submit(){
      if(this.name&&this.phone){
        if((/^1[3456789]\d{9}$/.test(this.phone))){
          bang_phone({username: this.name, mobile:this.phone}).then((res)=>{
            if(res.code==200){
            Toast('注册成功');
            this.$router.push({path:'/mall'})
            }
          })
        } else{
          Toast('手机格式有误，请重新填写')
        }
      } else {
        Toast('请完善注册信息')
      }
    }
  },
}
</script>

<style scoped lang="scss">

.top{
  height:100vh;
  background: #FAFAFA;
  .phone{
    margin:0 auto;
    margin-top:30pt;
    width:70%;
    border-bottom:1pt solid #DEDEDE;
  }
  .mt0{
    margin-top:0pt;
  }
  .reg{
    width:40%;
    line-height: 30pt;
    border-radius: 15pt;
    background: #15BDDB;
    color:#fff;
    text-align: center;
    font-size: 12pt;
    margin:40pt auto;
  }
}
/deep/ .van-cell{
  background: transparent;
}

/* 协议 */
.xieyi{
  display: flex;
  align-items: center;
  padding-left:20pt;
  span{
    color:#15BDDB;
  }
}
</style>



